<!-- HomeHeader.vue -->
<template>
  <div class="layout">
          <Header class="headerWrap">
            <div>
               <img class="fxLogo" src="../../assets/fxlogo.png" height="43" width="103" alt="">
            </div>

            <div>
              <span>欢迎  {{userName}} &nbsp;&nbsp;|&nbsp;&nbsp;</span>
              <span class="backLogin" @click="routerLogin">
                退出登录
              </span>
            </div>
          </Header>
          <div style="height:64px"></div>
          <Layout>
            <Sider class="layout-menu-left" hide-trigger>
                 <Menu  ref="leftMenu" :theme="theme2"  :active-name="active" :open-names="open" width="200px">
                    <Submenu v-for="(item,index) in menuList" :key="index" name="1">

                        <template slot="title">
                             <Icon type="ios-navigate"></Icon>
                             {{item.name}}
                        </template>
                        <router-link v-for="(childItem,childIndex) in item.childList" :key="childIndex" :to="{path:childItem.permissionUrl}">
                           <MenuItem :name="childItem.permissionUrl">{{childItem.name}}</MenuItem>
                         </router-link>
                    </Submenu>
                 </Menu>


            </Sider>
            <div id="content-right">
                <router-view></router-view>
            </div>
          </Layout>
    </div>

</template>
<script>
import Cookies from 'js-cookie'
    export default {
        data () {
            return {
                menuList:[],
                theme2: 'light',
                open: [],
                active: "1-1",
                isNotice:false,
                interv:null,
                userName:window.sessionStorage.getItem('userName')
            }
        },
        created(){
           // this.roleList()
          this.$Notice.config({
            top: 50,
            duration: 0
          });

        },

        mounted(){
          this.roleList()

             this.interv = setInterval(()=>{

            this.alertSaveMessage();

          },5000);


        },

      beforeDestroy(){
         console.log('beforeDestroy')

        window.clearInterval( this.interv);
      },

        methods:{
            roleList(){
                const userName  = sessionStorage.getItem('userName');
                // const token = sessionStorage.getItem('token');
                const url = "/user/getAllAuthList.json?userName="+userName
                this.$Http.get(url,{}).then(res=>{
                    // console.log(res.body.data.resourceList)
                    this.open=["1"];
                    this.active=this.$route.path;
                    this.menuList=res.body.data.resourceList;
                    console.log(this.open);

                    this.$nextTick(() => {
                      this.$refs.leftMenu.updateOpened();
                      this.$refs.leftMenu.updateActiveName();
                    })
                })
            },
          alertSaveMessage(){
                const url = "/common/alertSaveMessage.json?";
                this.$Http.get(url,{}).then(res=>{

                    if(res.body.status==0){
                      let isAlert = res.body.data.isAlert;
                      if(isAlert ==1){
                        //提示信息
                        if(!this.isNotice && this.$route.name != 'login'){
                          this.renderFunc();
                        }
                      }

                    }
                })
            },

          releaseAuthLock(){
                const url = "/common/releaseAuthLock.json?";
                this.$Http.get(url,{}).then(res=>{
                    if(res.body.status==0){
                      this.$Message.info(res.body.data);
                      this.$Notice.destroy();
                      this.isNotice =false;
                    }
                })
            },

          renderFunc () {

            this.isNotice =true;
            this.$Notice.warning({
                title: '广告权限发生变更是否保存生效？',
                duration:0,
                onClose: ()=> {
                  this.isNotice =false;
                },
                render: h => {
                  return h('div',[
                    h('Button',{
                      props:{
                        type:'primary',
                        size:'small'
                      },
                      style:{
                        marginRight:'5px',
                        backgroundColor: '#0676c4',
                        borderColor:'#0676c4',
                        float:'right',
                      },
                      on:{
                        click:()=>{
                          this.releaseAuthLock();
                        }
                      }

                    },"保存生效")

                  ]);
                }
            })



          },

            routerLogin(){
              // Cookies.remove('myUserName');
              // Cookies.remove('mypassWord');
              window.clearInterval( this.interv);
              window.sessionStorage.setItem('token',"");
              // window.sessionStorage.setItem('userName',"");
              this.$router.push({"name":'login'})

              this.$Notice.destroy()
            }
        }

    }
</script>
<style scoped>

  body,html,#app,.container,.layout{
    background: white!important;
    height: 100%;
}
.ivu-layout,.ivu-layout-has-sider{
  height: 100%;
}
  .ivu-layout-header{
    width: 100%;
    background: #00487a;
    color:#ffffff;
    font-size: 16px;
    position: fixed;
    top: 0;
    z-index: 300;
   }

  .ivu-menu{
    background: #e8e8e8;
  }

.layout-menu-left{
    position: fixed;
    left: 0;
    top: 64px;
    bottom: 0;
    background: #e8e8e8;
    overflow: scroll;
    overflow-x:hidden;
}
#content-right{
    width: 100%;
    padding-left: 200px;
    background: #ffffff;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
    color: #00487a;
    border-right: none;
    border-left: 4px solid #00487a;
    z-index: 2;
    background: white;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item{
    color: #8492a6;
}
.ivu-menu-dark {
    background: #080c30;
}
.ivu-menu-light{
    background: #e8e8e8;
}
.ivu-menu-item{
    background: #f6f6f6!important;
}
.ivu-menu-item:hover{
    background: white!important;
}
.fxLogo{
  display: inline-block;
  vertical-align: middle;
}
.headerWrap{
  display: flex;
  justify-content: space-between;
}
.backLogin:hover{
  cursor: pointer;
  color: #1e83ca;
}
</style>



