<!-- Home.vue -->
<template>
    <div class="container">
        <!-- 由于html不区分大小写，所以js中驼峰命名方式在html中要改成用短横线连接的形式 -->
        <sub-Menus></sub-Menus>
    </div>
</template>
<style>
.top-title{
    background: #f7f7f7;
    color: #00487a;
    height: 45px;
    line-height: 45px;
    font-size: 18px;
    padding-left: 10px !important;
}

.ivu-modal-wrap .ivu-modal-body .ivu-modal-confirm *{
  word-break:break-all;
}


.main-element {
    display: inline-block;
}

.main-element .el-select>.el-input {

}
.main-element .el-input__icon{
  line-height: 32px;
  font-size: 12px;
}
.main-element .el-input--suffix .el-input__inner {
    height: 32px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dddee1;
}

.main-element .el-select .el-input .el-select__caret {
    line-height: 32px;
    font-size: 12px;
}

.main-element .el-cascader{
  line-height: 32px;
}
</style>
<script>
  // 导入要用到的子组件
    import SubMenus from './SubMenus'
    export default {
        data () {
            return {
            }

        },

        // 在components字段中，包含导入的子组件
        components: {
            SubMenus
        }
    }
</script>



